import React from 'react';
import image from './image/oggy.jpg';


function About(){
    return(
        <div>
            <h1 align="center">oggy</h1>
            <img src={image} width='300' height='300'></img>
            <h3>"Oggy" is a slang term for a Cornish pasty, derived from its Cornish name, "hoggan", and was used by local Devon and Cornish sailors at the Devonport Dockyard in reference to pasty sellers who once stood outside the gates.</h3>
           
        </div>
    )
}
export default About;